.App {
  text-align: center;
  padding-bottom:5%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.hideContainer{
  display:none
}
.Back{
  font-size:large;
  
  border:1px solidrgba(185, 4, 4, 0.808);
  width:200px;
  height:50px;
  margin:10px;
  
  color:rgb(155, 0, 0);
  box-shadow:0 5px 5px rgba(0,0,0,.5);
  border-radius: 5px;
  outline:none;
}
.Next{
    font-size:large;
    border:none;
    width:200px;
    height:50px;
    margin:10px;
    background-color:  rgba(185, 4, 4, 0.808);
    color:white;
    box-shadow:0 5px 5px rgba(0,0,0,.5);
    border-radius: 5px;
    outline:none;
    
  
}
.Submit{font-size: medium;
  font-weight: bold;
  border:none;
  width:200px;
  height:50px;
  margin:10px;
  margin-top:35%;
  background-color: rgba(28, 29, 28, 0.829);
  color:white;
  box-shadow:0 5px 5px rgba(0,0,0,.5);
  border-radius: 5px;
  outline:none;
}
.Checkout{
  font-size: medium;
  font-weight: bold;
  border:none;
  width:200px;
  height:50px;
  background-color:  rgba(185, 4, 4, 0.808);
  color:white;
  box-shadow:0 5px 5px rgba(0,0,0,.5);
  border-radius: 5px;
  outline:none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.FoodSummary{
  padding:5%;
  box-shadow:0 6px 15px rgba(26, 25, 25, 0.452);
  border-radius: 2px 2px 15px 15px;
  border:0.5px solid rgba(211, 209, 209, 0.918);
  margin-top:5%;
  margin-left: 15%;
  margin-right:15%;
  display:grid;
  grid-template-columns: 50% 50%;
  text-align: left;
}
.summaryItem{
  display:grid;
  grid-template-columns: 70% 30%;
  border-bottom: 0.5px solid rgba(224, 219, 219, 0.795);
  margin-right:25px;
  background-color:rgba(128, 128, 128, 0.144);
  margin-bottom:5px;
}

@media screen and (max-width:700px){
  .FoodSummary{
    display:grid;
  grid-template-columns: 100%;
  }
}