.foodCategoryContainer{
margin:2%;
padding:5Px;

background-color:rgba(128, 128, 128, 0.212);

}
button{
    font-size: medium;
    font-weight: bold;
    border:none;
    width:150px;
    height:55px;
    margin:2%;
    border:1px solid rgba(165, 4, 4, 0.411);
    background-color: rgb(255, 255, 255);
    box-shadow:1px 5px 10px rgba(46, 46, 46, 0.692);
    border-radius: 3px;
    outline:none;
}
.selected{
    font-size: large;
    font-weight: bold;
    border:none;
    width:150px;
    height:55px;
    margin:2%;
    color:white;
    
    background-color: rgba(185, 4, 4, 0.808);
    box-shadow:1px 5px 10px rgba(46, 46, 46, 0.692);
    border-radius: 3x;
    outline:none;
    
}

.highlight1{
    color:rgb(182, 1, 1);
    font-size: xx-large;
}
.highlight2{
    color:rgba(68, 68, 68, 0.849);
    font-size: medium;
}
.submit{
    font-size: medium;
    font-weight: bold;
    border:none;
    width:200px;
    height:50px;
    margin:10px;
    background-color: rgba(28, 29, 28, 0.829);
    color:white;
    box-shadow:0 5px 5px rgba(0,0,0,.5);
    border-radius: 5px;
    outline:none;
}