.FoodItemsContainer{
    display:grid;
    grid-template-columns: 30% 30% 30%;
    padding-top:50px;
    padding-left:10%;
    background-color: rgba(255, 255, 255, 0);
}
.Btn{
    width:50px;
    height:50px;
    margin:10px;
    border-radius: 50%;  
    font-size: large;
    color:white;
    background-color:  rgba(185, 4, 4, 0.877);

}
.buttonContainer{
    margin-bottom:5%;
}
.FoodItem{
    border-radius: 0px 0px 15px 15px;
    margin:5%;
    margin-top:2%;
    box-shadow:0 5px 10px rgba(0,0,0,.5);
}
.Image{
    width:100%;
    height:auto;
}
@media screen and (max-width:1024px){
    .FoodItemsContainer{
        display:grid;
        grid-template-columns: 50% 50%;
      
        padding:15%;
        padding-left:18%;
        padding-top:5%;
        
    }
    .Image{
        width:100%;
        height:auto;
    }
    
}

@media screen and (max-width:700px){
    .FoodItemsContainer{
        display:grid;
        grid-template-columns: 100%;
      
        padding:15%;
        padding-left:18%;
        padding-top:5%;
        
    }
    .Image{
        width:100%;
        height:auto;
    }
    
}

