.Container{
    margin-left: 15%;
    margin-right:15%;
   
    padding:2%;
    margin-bottom: 25px;
    box-shadow:0 6px 15px rgba(26, 25, 25, 0.452);
    border-radius: 2px 2px 15px 15px;
    border:0.5px solid rgba(211, 209, 209, 0.918);
}
.topContainer{
    display:grid;
    grid-template-columns: 50% 50%;
    text-align: left;
    padding:10px;
}
.IMG{
margin-left:4%;
}

.select{
    margin-top:30px;
    margin-left:40%;
    font-size: xx-large;
    width:50%;
   
    background-color:  rgba(185, 4, 4, 0.808);
    color:rgb(247, 247, 247);
    font-weight: 500;
    box-shadow:0 5px 10px rgba(0,0,0,.5);

}
.main{
    float:left;
    padding:10%;
}
.nameContainer{
    
    display: grid;
    grid-template-columns: 70% 30%;
}
.summary{
    padding:2%;
    text-align: initial;
    margin-left:5%;
    margin-bottom:2%;
    background-color:rgba(167, 167, 167, 0.178);
}

.price{
    margin-top:40px;
    color:rgba(68, 68, 68, 0.849);
    font-size: medium;
}
@media  screen and (max-width:700px){
    
    .topContainer{
        
        background-color: rgb(255, 255, 255);
        display:grid;
        grid-template-columns: 100%;
        text-align: center;
        margin-bottom: 35px;
        
    }
    .select{
        margin-left:0;
        width:80%;
        font-size: x-large;
        width:55%;
        box-shadow:0 5px 10px rgba(0,0,0,.5);
    }
}
